<template>
  <div class="record-item">

    <div class="item-title flex-between">
      <span>学生得分</span>
      <van-icon v-if="!editMode" class-prefix="iconfont icon-duoxuananniu" class="icon-operate" @click="changeMode">
      </van-icon>
      <span v-else>
         <van-icon v-show="selectedIds.length>0" class-prefix="iconfont icon-pingjia" class="icon-operate"
                   @click="multiStudentEvaluate">
        </van-icon>
        <van-icon class-prefix="iconfont icon-fanhui1" class="icon-operate" @click="editMode = false">
        </van-icon>
      </span>
    </div>

    <van-checkbox-group v-model="selectedIds">

      <van-cell-group v-if="studentList && studentList.length>0">
        <van-cell class="cell_main" v-for="(item, index) in studentList" :key="index" @click="personSelect(item)"
                  :style="{paddingLeft: '0', paddingRight:'0'}">
          <template #icon>
            <img :src="defaultHead" alt=""/>
          </template>
          <template #title>
            <div class="flex-between line_title">
              <span>{{item.StudentName}}</span>
              <span>排名：
                <van-tag type="primary">{{item.Ranking}}</van-tag>
              </span>
            </div>
          </template>
          <template #label>
            <div class="flex-between line_title">
              <span> 总分：{{item.TotalScore}}</span>
              <span> 评分：{{item.score ? item.score : '未评'}}</span>
              <span> 评价：{{item.evaluateText ? '查看' : '未评'}}</span>
            </div>
          </template>
          <template #right-icon v-if="editMode">
            <van-checkbox sharp="square" :name="item['StudentID']" :style="{paddingLeft: '3vw'}"></van-checkbox>
          </template>
        </van-cell>
      </van-cell-group>
      <r-lack-data v-else></r-lack-data>
    </van-checkbox-group>

    <van-popup v-model="dialogVisible" position="bottom" closeable>
      <div class="sub_nav">
        <span>学生点评</span>
      </div>
      <div class="flex-between sub_module" v-if="studentSelected">
        <span>截屏提问：{{studentSelected["SAScore"]}}</span>
        <span>随堂练习：{{studentSelected["PAScore"]}}</span>
        <span>小组讨论：{{studentSelected["GDScore"]}}</span>
        <span>总分：{{studentSelected["TotalScore"]}}</span>
      </div>
      <van-field required name="stepper" label="课程评分：" :error-message="formCommentErr.score">
        <template #input>
          <van-stepper integer min="0" max="1000" v-model="formComment.score"/>
        </template>
      </van-field>
      <van-field required :error-message="formCommentErr.evaluate" name="rate" label="学生评价：">
        <template #input>
          <van-rate v-model="formComment.evaluate" :color="colors[formComment.evaluate]"/>
        </template>
      </van-field>
      <van-field required v-model="formComment.comment" :error-message="formCommentErr.comment" rows="2" label="学生评语："
                 type="textarea" maxlength="500" placeholder="请输入学生评语" autofocus autosize show-word-limit>
      </van-field>
      <van-button type="info" style="width: 100%;" @click="editMode ? saveFormMulti() : saveForm()">确定</van-button>
    </van-popup>
  </div>
</template>

<script>
import defaultImg from "../../../../../assets/boy_img.png";

export default {
  name: "StudentScore",
  data () {
    return {
      defaultHead: defaultImg,
      courseID: this.$store.getters.courseID,
      courseInfo: this.$store.getters.course,
      studentRanking: [],
      studentSelected: null,
      studentComments: [],
      dialogVisible: false,
      colors: ['#99A9BF', '#F7BA2A', '#FF9900'],
      texts: ['极差', '失望', '一般', '满意', '模范'],
      formComment: {
        id: "",
        courseID: "",
        studentID: "",
        comment: "",
        evaluate: 0,
        score: 100
      },
      formCommentErr: {
        comment: "",
        evaluate: "",
        score: ""
      },
      btnLoading: false,
      selectedIds: [],
      editMode: false
    };
  },
  computed: {
    studentList () {
      return this.studentRanking.map(item => {
        let index = this.studentComments.findIndex(r => r["StudentID"] === item["StudentID"]);
        let evaluate = index > -1 ? this.studentComments[index]["Evaluate"] : null;
        return {
          ...item,
          score: index > -1 ? this.studentComments[index]["Score"] : null,
          comment: index > -1 ? this.studentComments[index]["Comment"] : null,
          evaluate: evaluate,
          evaluateText: evaluate ? this.texts[evaluate - 1] : ""
        }
      });
    }
    // scorePreview () {
    //   return this.studentRanking.filter((r, k) => k < 3).map(item => {
    //     let index = this.studentComments.findIndex(r => r["StudentID"] === item["StudentID"]);
    //     let evaluate = index > -1 ? this.studentComments[index]["Evaluate"] : null;
    //     return {
    //       ...item,
    //       score: index > -1 ? this.studentComments[index]["Score"] : null,
    //       comment: index > -1 ? this.studentComments[index]["Comment"] : null,
    //       evaluate: evaluate,
    //       evaluateText: evaluate ? this.texts[evaluate - 1] : ""
    //     }
    //   });
    // }
  },
  created () {
    this.GetStudentRanking();
    this.getAllStudentEvaluate();
  },
  methods: {
    // 获取学生排名
    GetStudentRanking () {
      this.$mainHttp.classInfo.studentRanking({
        ciID: this.courseID,
        classID: this.courseInfo.classID
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach((item, index) => {
            this.studentRanking.push(item);
          });
        }
      });
    },

    // 获取所有学生点评
    getAllStudentEvaluate () {
      this.studentComments = [];
      this.$mainHttp.courseComment.teacherList({
        ciID: this.courseID
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.studentComments.push(item);
          });
        }
      });
    },
    selectChange (selected) {
      this.selectedIds = selected;
    },
    multiStudentEvaluate () {
      if (this.selectedIds.length < 1) {
        this.$toast('请先选择学生');
      } else {
        let selectedCommenteds = this.studentComments.filter(r => this.selectedIds.indexOf(r["StudentID"]) > -1).map(r => r["CEID"]);
        if (selectedCommenteds.length > 0 && selectedCommenteds.length !== this.selectedIds.length) {
          this.$toast('未评价的学生和已评价的学生请分开评价！');
        } else {
          this.formComment = {
            id: selectedCommenteds.length > 0 ? selectedCommenteds.join(",") : "",
            studentID: selectedCommenteds.length > 0 ? "" : this.selectedIds.join(","),
            ciID: this.courseID,
            comment: "",
            evaluate: 0,
            score: 100
          };
          this.dialogVisible = true;
        }
      }
    },
    personSelect (item) {
      if (this.editMode) {
        const index = this.selectedIds.findIndex(r => r === item["StudentID"]);
        if (index > -1) {
          this.selectedIds.splice(index, 1);
        } else {
          this.selectedIds.push(item["StudentID"]);
        }
      } else {
        this.studentEvaluate(item);
      }
    },
    studentEvaluate (item) {
      this.studentSelected = item;
      this.formComment = {
        id: "",
        studentID: item["StudentID"],
        ciID: this.courseID,
        comment: "",
        evaluate: 0,
        score: 100
      };
      this.getStudentEvaluate();
      this.dialogVisible = true;
    },
    getStudentEvaluate () {
      let reqData = {
        ciID: this.formComment.ciID,
        studentID: this.formComment.studentID
      };
      this.$mainHttp.courseComment.TeacherInfo(reqData).then(res => {
        if (res.Result && res.Data) {
          this.formComment.id = res.Data["CCID"];
          this.formComment.comment = res.Data["Comment"];
          this.formComment.evaluate = res.Data["Evaluate"];
          this.formComment.score = res.Data["Score"];
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      });
    },
    changeMode () {
      this.selectedIds = [];
      this.editMode = true;
    },
    saveFormMulti () {
      if (!this.formComment.comment) {
        this.formCommentErr.comment = "请填写评语";
        return;
      }

      this.btnLoading = true;
      let reqData = {
        ceIDs: this.formComment.id,
        ciID: this.formComment.ciID,
        studentIDs: this.selectedIds.join(","),
        score: this.formComment.score,
        comment: this.formComment.comment,
        evaluate: this.formComment.evaluate,
        anonymous: false,
        type: 1
      };
      this.$mainHttp.courseComment.BatchAdd(reqData).then(res => {
        if (res.Result) {
          this.dialogVisible = false;
          this.getAllStudentEvaluate();
          this.editMode = false;
          this.selectedIds = [];
          this.$toast({ message: "评价成功！", type: "success" });
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("评价失败");
        }
        this.btnLoading = false;
      }).catch(() => {
        this.$toast("操作失败");
        this.btnLoading = false;
      });
    },
    saveForm () {
      if (!this.formComment.comment) {
        this.formCommentErr.comment = "请填写评语";
        return;
      }
      let reqData = {
        ceID: this.formComment.id,
        ciID: this.formComment.ciID,
        studentID: this.formComment.studentID,
        score: this.formComment.score,
        comment: this.formComment.comment,
        evaluate: this.formComment.evaluate,
        anonymous: false,
        type: 1
      };
      this.$mainHttp.courseComment.dataAdd(reqData).then(res => {
        if (res.Result) {
          this.dialogVisible = false;
          this.$toast({ message: "评价成功！", type: "success" });
          this.getAllStudentEvaluate();
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

