  background-color: #07c160;
  border-color: #07c160;
}

.list_card {
  text-align: center;

  .card_info {
    color: #2c3e50;
    line-height: 2;
    font-size: 14px;
    text-align: left;

    .card_line {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
    }
  }

}

.dialog_list {
  /deep/ .el-table .cell.el-tooltip {
    white-space: inherit;
  }
}
</style>
