<template>
  <div class="record-item ">
    <div class="item-title flex-between">
      <span>考勤信息</span>

      <van-dropdown-menu v-if="signTimeOptions && signTimeOptions.length > 0">
        <van-dropdown-item v-model="signID" :options="signTimeOptions" @change="changeSignTime"/>
      </van-dropdown-menu>
    </div>

    <div class="sign_List" v-show="signTimeOptions && signTimeOptions.length > 0">
      <van-circle v-model="currentRate" :rate="rate" :stroke-width="60" :text="signStatistics.yes + '人已签'"
                  layer-color="#f3f3f3" :clockwise="false">
      </van-circle>
      <div class="flex-evenly">
        <div>
          <van-button type="info" size="mini" :style="{height: '6px'}"></van-button>
          <span>已签：{{signStatistics.yes + '人'}}</span>
        </div>
        <div>
          <van-button type="default" size="mini" :style="{height: '6px', background: 'rgb(243, 243, 243)'}"></van-button>
          <span>未签：{{signStatistics.no + '人'}}</span>
        </div>
      </div>
    </div>

    <van-cell-group v-if="personList && personList.length>0">
      <van-cell class="cell_main" v-for="(item, index) in personList" :key="index"
                :style="{paddingLeft: '0', paddingRight:'0'}">
        <template #icon>
          <img :src="item.imgSrc" alt=""/>
        </template>
        <template #title>
          <span class="line_title">{{item.name}}</span>
        </template>
        <template #label>
          <span class="line_title">{{item.sno}}</span>
        </template>
        <template #right-icon>
          <van-tag plain type="primary" v-if="item.remark">{{item.remark}}</van-tag>
          <van-tag plain v-else>未签到</van-tag>
        </template>
      </van-cell>
    </van-cell-group>
    <r-lack-data v-else></r-lack-data>

  </div>
</template>

<script>
import defaultImg from "../../../../../assets/boy_img.png";
import { formatDate } from "@/utils/formatData";

export default {
  name: "Attendance",
  data () {
    return {
      courseID: this.$store.getters.courseID,
      courseInfo: this.$store.getters.course,

      signID: "",
      signTimeOptions: [],

      currentRate: 0,
      rate: 0,
      personList: [],
      signStatistics: {
        no: 0,
        yes: 0,
        total: 0
      },
      isLoading: false
    };
  },
  created () {
    this.getSignTimes();
  },
  methods: {
    getSignTimes () {
      this.signTimeOptions = [];
      this.$mainHttp.sign.dataList({
        ciID: this.courseID
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.signTimeOptions.push({
                text: `第${item["Number"]}次签到`,
                value: item["SNID"],
                number: item["Number"],
                courseID: item["CIID"]
              });
            });
            this.signID = res.Datas[0]["SNID"];
            this.changeSignTime(this.signID);
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    changeSignTime (value) {
      this.personList = [];
      this.rate = 0;
      this.getSignPerson(value);
      this.getSignStatistics(value);
    },
    getSignPerson (value) {
      this.isLoading = true;
      this.$mainHttp.sign.signPersonList({
        ciID: this.courseID,
        classID: this.courseInfo.classID,
        snID: value
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.personList.push({
                imgSrc: defaultImg,
                name: item.Key["StudentName"],
                id: item.Key["StudentID"],
                sno: item.Key["StudentXH"],
                remark: formatDate(item.Value["SignTime"], "yyyy-MM-dd hh:mm:ss")
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    getSignStatistics (value) {
      this.$mainHttp.sign.signStatistics({
        snID: value,
        classID: this.courseInfo.classID
      }).then(res => {
        if (res.Result) {
          this.signStatistics = {
            no: res["NoSign"] || 0,
            yes: res["Sign"] || 0,
            total: (res["NoSign"] || 0) + (res["Sign"] || 0)
          };
          this.rate = this.signStatistics.total > 0 ? this.signStatistics.yes * 100.0 / this.signStatistics.total : 0;
        }
      }).catch(() => {
      });
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .van-dropdown-menu {
  height: unset;
}

.sign_List {
  margin: 20px auto;
  text-align: center;
}
</style>
